import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController,
} from "ionic-angular";
import { CodeCheckViewModel } from "../../Model/ViewModel/CodeCheckViewModel";

/**
 * Generated class for the CodeCheckModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page code-check-add-edit-modal",
  templateUrl: "code-check-add-edit-modal.html"
})
export class CodeCheckAddEditModalPage {
  item: CodeCheckViewModel;
  mode: string;
  title: string;
  CanEditBatch: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,

  ) {
    this.item = new CodeCheckViewModel();
    this.item.schemaname=navParams.data.item.schemaname;
this.item.tablename=navParams.data.item.tablename;
this.item.columnname=navParams.data.item.columnname;
this.item.check_type=navParams.data.item.check_type;
this.item.is_active=navParams.data.item.is_active;
this.item.creator=navParams.data.item.creator;
this.item.create_time=navParams.data.item.create_time;
this.item.modifier=navParams.data.item.modifier;
this.item.last_update_time=navParams.data.item.last_update_time;

    this.mode=navParams.data.mode;
    this.CanEditBatch = navParams.data.CanEditBatch;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }
  SelectCheckType() {
    let modal = this.modalCtrl.create("CheckTypeSelectModalPage", {
      item: this.item,
      selecdt_data:this.item.check_type,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.check_type = select_data.code_type;
    });

    modal.present();
  }
  SelectCheckCodeColumn() {
    let modal = this.modalCtrl.create("CheckCodeColumnSelectModalPage", {
      item: this.item,
      select_data:this.item.columnname,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.columnname = select_data.columnname;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad CodeCheckAddEditModalPage");
  }
}



